import {SET_ACTIVETAB,ASSIGN_DATA,UPDATE_LANG} from "../constants/action-types";
const initialState = {
  activetab:0,
  data:[],
  lang:"en-US"
};
const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case ASSIGN_DATA:
      return {
        ...state,
        data: action.payload
      };
    case SET_ACTIVETAB:
      return {
        ...state,
        activetab: action.payload
      };
      case UPDATE_LANG:
        return {
          ...state,
          lang: action.payload
        };
    default:
      return state;
  }
}
export default rootReducer;