import React ,{Component} from 'react' ;
import './gamelist.scss';
import Gametile from '../gametile/gametile.js';
import {filterlist} from '../../helpers';
import {appContext,langtext} from '../../config'
import {getdiff} from '../../helpers'
class gamelist extends React.Component {
    static contextType = appContext;
    constructor(props) {
       super(props);
       this.state = {
           message : "gamelist",
           gamelist:[]
       }
    }
    getdate(c){
        const date = new Date(c)
        const options = {year: 'numeric', month: 'short', day: 'numeric' };
        return date.toLocaleDateString('en-US', options);
    }
     static getDerivedStateFromProps(props, state){
        const gamelisting = []
         props.data.forEach(game => {
            const isfiltered = filterlist(game,props.activetab);
            if(isfiltered) {
                gamelisting.push(game)
            }
        });
        return {
            gamelist:gamelisting
        }
     }
     updategame(game){
        const index= game.index;
        game.createddate = this.getdate(game.createdOn);
        game.daydiff =getdiff(game.createdOn)
        if(Math.round(Math.abs(game.daydiff)) == 0) {
            game.status = 1;
        }
        else if(game.daydiff<0) {
            game.status = 2;
        } else {
            game.status = 0;
        }
        const list = [...this.props.data];
        list[index] = game;
        this.props.assigndata(list)
     }
    render() {
        const locale = this.context.locale
        const gamelist = this.state.gamelist;
        return (
                <div className="gamelist">
                    <table>
                        <thead>
                            <tr>
                                <th className="upprcase">{langtext["date"][locale]}</th>
                                <th className="upprcase">{langtext["campaign"][locale]}</th>
                                <th className="upprcase">{langtext["view"][locale]}</th>
                                <th colSpan="3" className="upprcase actions">{langtext["actions"][locale]}</th>
                            </tr>
                        </thead>
                        <tbody>
                            {gamelist.map((game,i)=>(<Gametile index={i} updategame={this.updategame.bind(this)} game={game} key={i}/>))}
                        </tbody>
                    </table>
                </div>
        );
    }
}
export default gamelist;
