import React ,{Component} from 'react' ;
import './gamepricingdetials.scss';
import Popup from '../basepopup/basepopup.js';
import {appContext,langtext} from '../../config'
class gamepricingdetials extends Component {
    static contextType = appContext;
    constructor(props) {
       super(props);
       this.state = {
           message : "gamepricingdetials"
       }
    }
    render() {
        const locale = this.context.locale;
        const game = this.props.data.game;
        return (
                <div className="gamepricingdetials">
                    <div className="game-det">
                        <div className="ib">
                            <img src ={"../../../resources/images/game/"+game.image_url}></img>
                        </div>
                        <div className="ib">
                            <p className="name">{game.name}</p>
                            <p className="region">{game.region}</p>
                        </div>
                    </div>
                    <div>
                        <h2>{langtext["Pricing"][locale]}</h2>
                        <table className="">
                            <tbody>
                                <tr>
                                    <td>1 Week - 1 Month</td>
                                    <td>$100</td>
                                </tr>
                                <tr>
                                    <td>6 Months</td>
                                    <td>$400</td>
                                </tr>
                                <tr>
                                    <td>1 Year</td>
                                    <td>$500</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="text-center">
                        <button className="" onClick={this.props.data.closepopup.bind(this)}>Close</button>
                    </div>
                </div>
        );
    }
}
export default Popup(gamepricingdetials);
