import React, { Component } from 'react';
import './app.scss';
import Container from '../container/container.js';
import {jsondata,appContext} from '../../config'
import Header from '../header/header';
import {processgame} from '../../helpers'
class App extends Component{
   constructor(props) {
      super(props);
      this.state = {
         showPlugins:false,
         locale:this.props.lang
      }
   }
   componentDidMount(){
      const gamelisting = jsondata.data.map((game,i) => {
        const pgame =  processgame(game);
        pgame.index = i
        return pgame
      })
      this.props.assigndata(gamelisting)
   }
   static getDerivedStateFromProps(props, state){
      return {
         locale:props.lang
      }
   }
   setpopupstate(state) {
      this.setState({
         showPlugins:state
      })
   }
   render(){
      const appprovider = {
         setpopup:this.setpopupstate.bind(this),
         showPlugins:this.state.showPlugins,
         locale:this.state.locale
      }
      return(
            <appContext.Provider value={appprovider}>
               <section onClick={this.setpopupstate.bind(this,false)}>
                  <Header/>
                  <Container/>
               </section>
            </appContext.Provider>
      );
   }
}
export default App;