import {
  SET_ACTIVETAB,ASSIGN_DATA,UPDATE_LANG
} from "../constants/action-types";
export const setactivetab = payload => ({
  type: SET_ACTIVETAB,
  payload
});
export const assigndata = payload => ({
  type: ASSIGN_DATA,
  payload
});
export const updatelang = payload => ({
  type: UPDATE_LANG,
  payload
});
