const getdate = (c) => {
    const date = new Date(c)
    const options = {year: 'numeric', month: 'short', day: 'numeric' };
    return date.toLocaleDateString('en-US', options);
}
export const getdiff =(dt) =>{
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    const target = new Date(dt);
    target.setHours(0, 0, 0, 0);
    return (target.getTime() - today.getTime()) / 86400000;
}
export const processgame = (game) => {
    game.createddate = getdate(game.createdOn);
    game.daydiff =getdiff(game.createdOn);
    if(new Date().getDate() == new Date(game.createdOn) && Math.round(Math.abs(game.daydiff)) == 0) {
        game.status = 1;
    }
    else if(game.daydiff<0) {
        game.status = 2;
    } else {
        game.status = 0;
    }
    game.daydiff = game.daydiff
    return game

}

export const filterlist = (game,status) => (game.status == status)