import React ,{Component} from 'react' ;
import "./basepopup.scss";
const Basepopup = (WrappedComponent,data) => {
    class Popupwrap extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                show:false
            };
        }
        componentDidMount() {
            setTimeout(() => {
                this.setState({
                    show:true
                })
            });
        }
        closepopup() {
            if(this.props.config.closepopup){
                this.setState({
                    show:false
                });
                setTimeout(() => {
                    this.props.config.closepopup();
                }, 300);
            }
        }
        render() {
            const popupconfig = this.props.config;
            return (                    
                <div className={"basepopup"+(this.state.show?" show":"")}>
                    <div className="popup-bg" onClick = {this.closepopup.bind(this)}></div>
                    <div className={"popup-container ovf-hidden "+ (popupconfig.classname)}>
                        <div className={"popup-header "+(popupconfig.heading.classname)}>
                            <h3 className={"heading fs-18 bold" + (popupconfig.heading.center?" text-center":" text-left")}>{this.props.config.closepopup && <i onClick={this.closepopup.bind(this)} className="cursor-pointer pull-right pr-15 fs-18 cot-cancel"></i>}{popupconfig.heading.text}</h3>
                            {popupconfig.subheading && <small className={"block lt-gray tc " + (popupconfig.heading.center?" text-center":"")}> 
                            </small>}
                        </div>
                        <div className="popup-content " >
                            {popupconfig.ajaxloaded && <div className="pr ajax-loader">
                                <div className="abs css-ajax-loader spin">
                                </div>
                            </div>}
                            {popupconfig.ajaxError && <div className="ajax-error">
                                <p>OOPS ! Something went wrong</p>
                            </div>}
                            <div id="popupdata">
                            <WrappedComponent data = {popupconfig.data} callback = {popupconfig.callback}/>
                        </div>
                        </div>
                    </div>
                </div>
            );
        }
    }
      
    return Popupwrap;
};
export default Basepopup