import React ,{Component} from 'react' ;
import gametilecss from './gametile.scss';
import statistic from '../../../resources/images/statistics.png';
import calendar from '../../../resources/images/calendar.png';
import file from '../../../resources/images/file.png'
import price from '../../../resources/images/Price.png';
import pubg from '../../../resources/images/pubg.png';
import Datepicker from '../datepicker/datepicker.js';
import {appContext,langtext} from '../../config'
import Gamepricedet from '../gamepricingdetials/gamepricingdetials.js'
class gametile extends React.Component {
    static contextType = appContext;
    constructor(props) {
       super(props);
       this.state = {
        game:this.props.game,
        showdp:false
       }
    }
    getdate(c) {
        const date = new Date(c)
        const options = {year: 'numeric', month: 'short', day: 'numeric' };
        return date.toLocaleDateString(this.context.locale, options);
    }
    stoppropogation(e) {
        e.stopPropagation()
    }
    setdate(arg) {
        const game = this.props.game;
        this.context.setpopup(false);
        game.createdOn = new Date(arg).getTime();
        this.props.updategame(game)

    }
    showpopup(e){
        e.stopPropagation();
        const state = this.context.showPlugins == "pricepopup"+this.props.index ? false: "pricepopup"+this.props.index;
        this.context.setpopup(state);
    }
    showddp(e) {
        e.stopPropagation();
        const state = this.context.showPlugins == "dp"+this.props.index ? false: "dp"+this.props.index;
        this.context.setpopup(state);
    }
    closepricing() {
        this.context.setpopup(false);
    }
    render() {
        const locale = this.context.locale;
        const game = this.props.game;
        const config = {
            callback: this.setdate.bind(this),
            range: false,
            showdesctext: false,
            firstactivedate:new Date().toDateString(),
            selecteddate:game.createdOn,
            showmonths:1
         }
         game.daydifftext=game.daydiff>0? (Math.round(Math.abs(game.daydiff)) + " "+langtext["days"][locale] +" "+ langtext["ahead"][locale]):(game.daydiff == 0?langtext["live"][locale]:Math.round(Math.abs(game.daydiff))  + " "+langtext["days"][locale] + " "+ langtext["before"][locale])
        return (
                <tr className="gametile">
                    <td className="date-sec">
                        <p className="date">{game.createddate}</p>
                        <p className="italic">{game.daydifftext}</p>
                    </td>
                    <td className="game-sec flex">
                        <img alt="game-icon" src ={"./resources/images/game/"+game.image_url} className="game-icon"></img>
                        <div className="text">
                            <p className="name">{game.name}</p>
                            <p className="italic">{game.region}</p>
                        </div>
                    </td>
                    <td className="light-font" onClick={this.showpopup.bind(this)}>
                        <img src={price}></img>
                        <p>{langtext["view pricing"][locale]}</p>
                        {this.context.showPlugins == ("pricepopup"+this.props.index) && <Gamepricedet config={{
                        classname :"pricing",
                        closepopup:this.state.closepricing,
                        heading:{
                            classname:"hide"
                        },
                        data:{
                            game:game,
                            closepopup:this.closepricing.bind(this),
                        }
                    }}/>}
                    </td>
                    <td className="light-font" > 
                        <a className="ib" href= {game.csv}> 
                            <img src= {statistic}></img>
                            <p>CSV</p>
                        </a>
                    </td>
                    <td className="light-font" >
                        <img src={file}></img>
                        <p>{langtext["report"][locale]}</p>
                    </td>
                    <td className="light-font sch" onClick={this.showddp.bind(this)}>
                        <img src={calendar}></img>
                        <p>{langtext["schedule again"][locale]}</p>
                        {this.context.showPlugins == ("dp"+this.props.index) && <div className="dpwrp"> 
                            <Datepicker pluginconfig={config}/>
                        </div>}
                    </td>
                </tr>
        );
    }
}
export default gametile;
