import React ,{Component} from 'react' ;
import './tabs.scss';
import {appContext,langtext} from '../../config'
class tabs extends React.Component {
    static contextType = appContext;
    constructor(props) {
       super(props);
       this.state ={
           tabs :[
               {text:"Upcoming Campaigns",id:0},
               {text:"Live Campaigns",id:1},
               {text:"Past Campaigns",id:2}
           ],
           activetab:this.props.activetab
       }
    }
    selecttab(tab){
        this.setState(prevState => ({
            ...prevState,
            activetab:tab.id
        }))
        this.props.setactivetab(tab.id)
    }
    render() {
        const locale = this.context.locale
        return (
        <div className="bold flex tabs">
            {this.state.tabs.map((tab,i)=>(
                <div key={i} onClick={this.selecttab.bind(this,tab)} className={"tab "+(this.state.activetab == tab.id?" font-green active":"")}>{langtext[tab.text][locale] }</div>
            ))}
        </div>
        );
    }
}
export default tabs;
